.proyecto {
  margin: 1em auto; 
}

.proyecto >img{
  width: 100%;
}

.proyecto li {
  width: 30%;
}

.proyecto >div >img {
  width: 1.5em;
}

.proyecto div {
  width: calc(100% - .7em);

  border: 2px solid white;
  border-radius: .3em;
  
  background-color: white;

  padding: .2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.proyecto a {
  color: rgb(255, 0, 0);
  text-decoration: none;
}


