i {
  height: 1.5em !important;
  -ms-flex-item-align: auto;
      align-self: auto;
  margin: 0 .5em; 
}

#tecnologias {
  width: 60%;
  min-width: 15em;
  display: -ms-flexbox;
  display: flex;
  margin: 1em auto;

}


#iconos {
  padding: 1em;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.tecnologias-img {
  padding: .2em;

  width: 4em;
  height: 4em;

  margin: .5em 0;

  border-radius: 2em;
  border: .3em solid blueviolet; 
}

.tecnologias-img:hover {
  opacity: .5;
}

.descripcion{
  width: 80%;
  margin: 0 auto;
  padding: 1em;
  font-family: 'Ubuntu Condensed', sans-serif;
  background-color: blueviolet;
  color: white;
  border-radius: .5em;
  
}

.descripcion h2 {
  margin: 0;
}

.horizontal-list {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0;
  margin: 1em 0;
}

.horizontal-list li {
  margin: 0 1em;
}

/*colores de la imagenes*/

#img-js {
  border-color: yellow;
}

#img-html {
  border-color: orangered;
}

#img-css {
  border-color:slateblue;
}

#img-php {
  border-color: cadetblue;
}

#img-java{
  border-color: coral; 
}


