footer {
  padding: 1em;
  width: calc(100% - 2em);
  background-color:slateblue;
  color: white;
  margin: 2em 0 0 0;
  font-family: 'Ubuntu Condensed', sans-serif;
  height: auto;
}

footer img {
  width: 2.5em;
}

#footer-contacto p{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center; 
  font-weight: 100;
  opacity: .7;
}

#footer-contacto p:first-child{
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: 800;
  opacity: 1;
}

#footer-contacto img {
  margin: 0 .3em 0 0;
}

#footer-rss {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;  
}

#footer-rss * {
  margin: .5em .2em; 
}

#footer-iconos {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-align: center;
      align-items: center;  
}

#footer-container {
  margin: 0 auto;
  min-width: 15em;
  width: 60%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}


