#square img {
  /*position: absolute;*/
  /*top:-2.5em;
  left: calc(50% - 6em);*/
  width: 10em;
  height: 10em;
  
  display: block;
  margin: 0 auto;

  border-radius: 20em;
  border: .5em solid white;
  -webkit-box-shadow: 0 0 0 .5em blueviolet;
          box-shadow: 0 0 0 .5em blueviolet;
}

header {
  /*anchura*/ 
  width: calc(100% - 2em);
  padding: 1em;
  background-color: blueviolet;
}

#square {
  position: relative;
  display: block;
  min-width: 15em;
  width: 60%;
  height: 11em;
  background-color: white;
  margin: 1em auto;
  border-radius: .2em;
}

#description {
  min-width: 15em;
  width: 60%;
  margin: 1.5em auto;
  font-family: monospace;
  font-size: 15px;
  font-weight: 500;
}

#description p{
  color: white;
  text-align: justify;
}

hr {
  border: 2px solid white;
  border-radius: 1em;
}

#dot {
  border: 10px solid white;
  width: 0em;
  height: 0em;
  margin-top: -19px;
}


